<template>
    <div>
        <span></span>
        <h3> debounce</h3>
        <p></p>
        <p><a href="https://rockjins.js.org/2017/02/21/2017-02-21-debounce-function/">Rockjins Blog</a></p>
        <div class="code">
            <code>
            function debounce(fn, interval, immediate) {
                //fn
                //interval
                //immediate
                var timeout;  //
                return function() { //
                    var context = this, args = arguments; //
                    var later = function() {  //
                    timeout = null; //
                    if(!immediate) fn.apply(context, args); //
                    };
                    var callNow = immediate && !timeout;  //
                    clearTimeout(timeout);  //
                    timeout = setTimeout(later, interval);  //
                    if(callNow) fn.apply(context, args);  //immediatetrue
                };
                };
                //
                var myEfficientFn = debounce(function() {
                //
                }, 250);
                window.addEventListener('resize', myEfficientFn);
        </code>
        </div>
    </div>
